import React from 'react';
import { Grid, Box, useTheme, useMediaQuery } from '@mui/material';
import { StickerSetResponse } from '@/types/sticker';
import { StickerCard } from './StickerCard';

interface StickerSetListProps {
  stickerSets: StickerSetResponse[];
  onView: (id: number, name: string) => void;
  onShare: (name: string, title: string) => void;
  onDelete: (id: number, title: string) => void;
  isInTelegramApp?: boolean;
}

export const StickerSetList: React.FC<StickerSetListProps> = ({
  stickerSets,
  onView,
  onShare,
  onDelete,
  isInTelegramApp = false
}) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  //   
  const getSpacing = () => {
    return isSmallScreen ? 1 : 2; // 8px   , 16px  
  };

  console.log(' StickerSetList :', {
    stickerSetsCount: stickerSets.length,
    isInTelegramApp,
    isSmallScreen,
    spacing: getSpacing()
  });

  return (
    <Box sx={{ 
      pb: isInTelegramApp ? 2 : 10 //    Bottom Navigation
    }}>
      <Grid container spacing={getSpacing()}>
        {stickerSets.map((stickerSet) => {
          console.log(' StickerSetList  :', {
            stickerSetId: stickerSet.id,
            stickerSetTitle: stickerSet.title,
            isInTelegramApp
          });
          
          return (
            <Grid 
              item 
              xs={6}    //   2  (50% )
              sm={6}    //  900px - 2  
              md={4}    // 900px+ - 3 
              lg={3}    // 1200px+ - 4 
              key={stickerSet.id}
            >
              <StickerCard
                stickerSet={stickerSet}
                onView={onView}
                onShare={onShare}
                onDelete={onDelete}
                isInTelegramApp={isInTelegramApp}
              />
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
};
